@import url(https://fonts.googleapis.com/css2?family=Roboto:wght@300;400;500;700;900&display=swap);
@import url(https://fonts.googleapis.com/css2?family=Poppins:wght@300;400;500;600;700&display=swap);
:root{--primary-dark: #162430;--primary-orange: rgb(245, 160, 92);--primary-blue: #2196F3;--drk-blue: rgb(32, 141, 230);--nav-height: 72px;--nav-height-mobile: 64px;--ff-primary: 'Roboto', sans-serif;--ff-accent: 'Poppins', sans-serif}*{padding:0;margin:0;box-sizing:border-box}*:focus{outline:none}html,body{height:100vh;font-size:1.2rem;letter-spacing:-.4px;scroll-behavior:smooth;-webkit-font-smoothing:antialiased;color:#162430;color:var(--primary-dark);background-color:#fff;font-family:'Roboto', sans-serif;font-family:var(--ff-primary);font-weight:400;line-height:1.7}body{overflow-x:hidden}img,video{max-width:100%;height:auto}ul{list-style-type:none}a{text-decoration:none}h1,h2,h3{line-height:normal}.wrapper{padding:0px 20px}.wrapper2{width:80%}.margin-auto{margin:0 auto}.flex{display:flex}.space-between{justify-content:space-between}.items-center{align-items:center}.content-center{justify-content:center}.column{flex-direction:column}.grid{display:grid}.block{display:block}.inline-block{display:inline-block}.hidden{display:none}.overflow-auto{overflow:auto}.relative{position:relative}.fixed{position:fixed}.absolute{position:absolute}.full-height{height:100%}.full-width{width:100%}.width-half{width:50%}.width-third{width:33.3333333333%}.pointer{cursor:pointer}.auto-pointer{cursor:auto}.align-center{text-align:center}.align-left{text-align:left}.align-right{text-align:right}.float-right{float:right}.float-left{float:left}.fade-appear,.fade-enter{opacity:0;z-index:1}.fade-appear-active,.fade-enter.fade-enter-active{opacity:1;transition:opacity 300ms linear 150ms}.fade-exit{opacity:1}.fade-exit.fade-exit-active{opacity:0;transition:opacity 300ms linear}

.homepage-banner{background-position:center;background-repeat:no-repeat;background-size:cover}.homepage-banner .content{flex-direction:column;justify-content:center;color:#fff}.homepage-banner .content .name{font-size:2.5rem;-webkit-margin-after:15px;margin-block-end:15px;text-shadow:0px 4px 4px rgba(0,0,0,0.25)}.homepage-banner .content .role{font-size:21px;text-shadow:0px 4px 4px rgba(0,0,0,0.25);-webkit-margin-after:22px;margin-block-end:22px}.homepage-banner .content .links{-webkit-margin-after:22px;margin-block-end:22px}.homepage-banner .content .links li a{font-size:0.7rem;color:#fff;-webkit-margin-end:1rem;margin-inline-end:1rem;text-shadow:0px 4px 4px rgba(0,0,0,0.25)}.homepage-banner .content .links li a:hover{text-decoration:underline;opacity:0.7}.homepage-banner .content .socials li{-webkit-margin-end:8px;margin-inline-end:8px}.homepage-banner .content .socials li a{text-shadow:0px 4px 4px rgba(0,0,0,0.25)}.homepage-banner .content .socials li a:hover{opacity:0.8}.about-me .image{height:453px;width:453px;background-position:center;background-repeat:no-repeat;background-size:cover}.about-me .content{flex-direction:column;justify-content:center;width:670px;word-wrap:break-word}.about-me .content h1{font-size:2.1rem;-webkit-margin-after:15px;margin-block-end:15px}.about-me .content p{font-size:21px}.my-skills{background:#1b232e;color:#fff}.my-skills h1{font-size:2.1rem;-webkit-margin-after:3rem;margin-block-end:3rem}.my-skills .badges{flex-wrap:wrap;justify-content:center}.my-skills .badges li{margin:8px;-webkit-margin-after:1rem;margin-block-end:1rem;transition:ease-out 0.3s}.my-skills .badges li:hover{transform:translateY(-5px);transition:ease-in 0.3s}.my-skills .badges li p{font-size:0.8rem;pointer-events:none}.projects .section-title{font-size:2.1rem;-webkit-margin-after:4rem;margin-block-end:4rem}.last-contact{background:#F8F8F8}.last-contact .content{flex-direction:column;justify-content:center;width:670px;word-wrap:break-word}.last-contact .content .title{font-size:1.9rem}.last-contact .content .description{font-size:0.9rem;-webkit-margin-after:1rem;margin-block-end:1rem}

header{align-items:center;height:65px;box-shadow:1px 2px 4px rgba(0,0,0,0.1)}header .wrapper-md{flex-direction:row !important}header .logo{height:45px;width:47px}header .logo a .img{margin:auto}header .nav li a{font-size:0.8rem;-webkit-margin-start:2rem;margin-inline-start:2rem;color:var(--primary-dark);transition:ease-in-out .3s}header .nav li a:hover{color:var(--primary-blue)}

.wrapper-md{width:1200px}@media only screen and (max-width: 1200px){.wrapper-md{width:1100px !important;padding:0px 20px}}

.hero-section{height:calc(100vh - 65px)}

footer{background:#1b232e;color:#fff;padding:45px 0px 45px 0px}footer .copyright{font-size:0.8rem}footer .links li{-webkit-margin-start:1rem;margin-inline-start:1rem}footer .links li a{font-size:0.8rem;color:#fff}footer .links li a:hover{text-decoration:underline}footer .socials li{-webkit-margin-start:8px;margin-inline-start:8px}footer .socials li a{text-shadow:0px 4px 4px rgba(0,0,0,0.25)}footer .socials li a:hover{opacity:0.8}

.section{padding:120px 0px}.section.xl{height:635px}

.project{background-position:center;background-repeat:no-repeat;background-size:cover;margin:16px;width:320px;height:320px;border-radius:4px;background-color:#000;color:#fff;justify-content:center;align-items:center;transition:ease-in-out 0.3s}.project .icon{opacity:0;transition:ease-in-out 0.3s}.project:hover{background-color:#000}.project:hover .icon{opacity:1}.project .image{height:450px;width:450px;background-position:center;background-repeat:no-repeat;background-size:cover}.project .content{width:670px;padding:12px 15px}.project .content .type{font-size:1.1rem;font-weight:400;color:#76B2EA;-webkit-margin-after:28px;margin-block-end:28px;-webkit-margin-before:0 !important;margin-block-start:0 !important}.project .content .title{font-size:1.8rem;-webkit-margin-after:29px;margin-block-end:29px}.project .content .description{font-size:1rem;color:#6D6D6D}.project .content .slugs{-webkit-margin-before:32px;margin-block-start:32px}.project .content .slugs li{background-color:#0094FF;color:#fff;font-size:15px;border-radius:10px;height:36px;padding:0px 10px;-webkit-margin-end:10px;margin-inline-end:10px;pointer-events:none}.project .content .links{bottom:12px}.project .content .links li{-webkit-margin-end:0.3rem;margin-inline-end:0.3rem}.project .content .links li a{color:#6D6D6D;-webkit-margin-end:0.8rem;margin-inline-end:0.8rem}.project .content .links li a:hover{opacity:0.8}.project .content .links li a p{font-size:0.8rem;-webkit-margin-start:5px;margin-inline-start:5px}

.btn{padding:12px 21px;align-items:center;border:0;border-radius:18px;font-weight:500}.btn.blue{border:1.7px solid #0094FF;color:#fff;background-color:#0094FF}.btn.transparent{color:#fff;border:1.7px solid #fff;background-color:transparent}

.contact-page{background-position:center;background-repeat:no-repeat;background-size:cover;height:100vh;width:100%}.contact-page .back-btn{color:#fff;left:51px;top:21px}.contact-page .back-btn p{font-size:0.8rem;top:1.5px}.contact-page .contact-form{left:0;right:0;top:0;bottom:0;margin:auto;width:505px;height:609px;background:#FFFFFF;border:1px solid #E7E7E7;box-shadow:0px 4px 4px rgba(0,0,0,0.25);border-radius:14px}.contact-page .contact-form .container{width:80%;padding:31px 0px}.contact-page .contact-form .container .title{font-size:32px;font-weight:normal;-webkit-margin-after:36px;margin-block-end:36px}.contact-page .contact-form .container .input{-webkit-margin-after:18px;margin-block-end:18px}.contact-page .contact-form .container .input label{font-size:0.85rem;-webkit-margin-after:8px;margin-block-end:8px;font-weight:400}.contact-page .contact-form .container .input input{border:1px solid #E7E7E7;height:45px;border-radius:8px;padding:12px;font-size:0.75rem;color:#575757;box-shadow:0px 1px 2px rgba(0,0,0,0.1)}.contact-page .contact-form .container .input textarea{border:1px solid #E7E7E7;height:195px;border-radius:8px;padding:12px;font-size:0.75rem;color:#575757;resize:none;box-shadow:0px 1px 2px rgba(0,0,0,0.1)}

.modal{display:none;position:fixed;z-index:1;left:0;top:0;width:100%;height:100%;overflow:auto;-webkit-animation-name:fadeIn;-webkit-animation-duration:0.4s;animation-name:fadeIn;animation-duration:0.4s}.modal.active{display:block}.modal .background{background-color:#000;background-color:rgba(0,0,0,0.4)}.modal-content{position:absolute;top:100px;left:0;right:0;background-color:#fefefe;width:80%}.close{color:#C8C8C8;float:right;font-size:28px;font-weight:bold}.close:hover,.close:focus{color:#000;text-decoration:none;cursor:pointer}.modal-header{height:45px;padding:0px 16px;overflow:auto}.modal-body{padding:0px 16px 36px 16px}@keyframes fadeIn{from{opacity:0}to{opacity:1}}.project-modal .modal-body .image{height:450px;width:450px;background-position:center;background-repeat:no-repeat;background-size:cover}.project-modal .modal-body .content{width:670px;padding:12px 15px}.project-modal .modal-body .content .type{font-size:1.1rem;font-weight:400;color:#76B2EA;-webkit-margin-after:28px;margin-block-end:28px;-webkit-margin-before:0 !important;margin-block-start:0 !important}.project-modal .modal-body .content .title{font-size:1.8rem;-webkit-margin-after:29px;margin-block-end:29px}.project-modal .modal-body .content .description{font-size:1rem;color:#6D6D6D}.project-modal .modal-body .content .slugs{-webkit-margin-before:32px;margin-block-start:32px}.project-modal .modal-body .content .slugs li{background-color:#0094FF;color:#fff;font-size:15px;border-radius:10px;height:36px;padding:0px 10px;-webkit-margin-end:10px;margin-inline-end:10px;pointer-events:none}.project-modal .modal-body .content .links{bottom:12px}.project-modal .modal-body .content .links li{-webkit-margin-end:0.3rem;margin-inline-end:0.3rem}.project-modal .modal-body .content .links li a{color:#6D6D6D;-webkit-margin-end:0.8rem;margin-inline-end:0.8rem}.project-modal .modal-body .content .links li a:hover{opacity:0.8}.project-modal .modal-body .content .links li a p{font-size:0.8rem;-webkit-margin-start:5px;margin-inline-start:5px}

